export default {
	data() {
		return {
			user_info: [],
			value: '',
			money: '',
			withdrawalFlag: false
		};
	},
	onShow() {
		// this.money = uni.getStorageInfoSync('moneys')
		// console.log(this.money)
	},
	onLoad(options) {
		this.user_info = uni.getStorageSync('user_info')
		this.money = options.money
	},
	methods: {
		getValue(e) {
			this.value = e.detail.value;
			this.judge()
		},
		toHelpCenter() {
			uni.navigateTo({
				url: '/pages/mine/income/helpCenter'
			})
		},
		// 判断输入框是否有内容
		judge() {
			if (this.value) {
				this.withdrawalFlag = true
			}else{
				this.withdrawalFlag = false
			}
		},
		// 点击全部提现
		toSetmoney() {
			this.value = this.money
			this.judge()
		},
		toWithdrawalRecord() {
			uni.navigateTo({
				url: '/pages/mine/income/withdrawalRecord'
			})
		},
		// 提现
		withDrawal() {
			console.log(this.value)
			console.log(parseFloat(this.value).toFixed(2))
			if (parseFloat(this.value).toFixed(2) < 1.00) {
				uni.showToast({
					title: '提现金额必须大于1',
					duration: 1000,
					icon: 'none'
				})
			} else if (parseFloat(this.value).toFixed(2) > this.money) {
				uni.showToast({
					title: '余额不足，重新输入',
					duration: 1000,
					icon: 'none'
				})
			} else {
				this.$api.commissionWithdrawal({money: this.value}).then(res => {
					console.log(res)
					if(res.data.errno === 0){
						uni.showToast({
							title: '提现成功',
							duration: 1000,
							icon: 'none'
						})
						
						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							})
						},1000)
					}
				})
			}
		}
	}
}
